@include font("GothamProMedium", GothamProMedium);
@mixin GP_m {
  font-family: GothamProMedium, sans-serif; }
@include font("GothamProBold", GothamProBold);
@mixin GP_b {
  font-family: GothamProBold, sans-serif; }
@include font("GothamPro", GothamPro);
@mixin GP {
  font-family: GothamPro, sans-serif; }

@mixin theme_text {
  color: #777f84;
  font-size: em(15);
  line-height: em(25, 15); }
@mixin theme_title {
  position: relative;
  display: block;
  text-align: center;
  font-size: em(20);
  @include GP_b;
  text-transform: uppercase;
  line-height: 3em;
  &:before {
    content: "";
    display: block;
    @include vcenter;
    @include s(icon-title);
    opacity: 0.2; } }

html {
  font-size: 100%; }

body {
  background: #FFF;
  @include GP;
  font-size: 100%;
  line-height: 1.2em;
  font-weight: 400; }


a {
  text-decoration: none;
  &:hover {
    text-decoration: underline; } }
.wrapper {
	max-width: 960px;
	margin: 0 auto;
	padding: 0 12px {
  @include clr; } }
.button {
  width: 227px;
  height: 57px;
  border: 3px solid #fdb352;
  background-color: rgba(185, 53, 99, 0);
  text-transform: uppercase;
  color: #fff;
  @include GP_b;
  font-size: em(15);
  transition: 0.4s;
  &:hover {
    border-radius: 5px;
    border-color: transparent;
    background-color: rgba(185, 53, 99, 0.8);
    filter: drop-shadow(-1px 1px 3px #000); } }

@include r(680) {
  .button {
    width: auto;
    height: auto;
    padding: 10px; } }

