.demo-acc {
	margin-top: 218px;
	color: #FFF;
	width: 70%;
	&__title {
		font-size: em(60);
		@include GP_b;
		line-height: em(65, 60); }
	&__text {
		margin-right: 140px;
		margin-top: 45px;
		font-size: em(17);
		line-height: em(25, 17); }
	&__button {
		margin: 59px 0;
		color: #fff;
		font-size: em(14);
		@include GP_b;
		text-transform: uppercase; } }

@include r(850) {
	.demo-acc {
		margin-top: 100px;
		&__title {
			font-size: 2.5em; } } }
@include r(650) {
	.demo-acc {
		margin-top: 50px;
		width: 100%; }
	.demo-acc__title {
		font-size: 2em; }
	.demo-acc__text {
		margin-right: 0; } }
@include r(500) {
	.demo-acc__title {
		font-size: 1.5em; }
	.demo-acc__text {
		margin-top: 10px; }
	.demo-acc__button {
		margin: 30px 0; } }
