.header {
	position: relative;
	padding-top: 44px;
	@include clr;
	&__logo {
		float: left; }

	&__lang {
		float: right;
		margin-right: 45px;
		margin-top: 17px;
		font-family: "Proxima Nova", sans-serif;
		font-size: em(15);
		font-weight: 600;
		text-transform: uppercase;
		.lang__link {
			display: block;
			display: inline-block;
			margin-left: 13px;
			color: #a38256; }
		.lang__link.is-active {
			color: #fff; } } }
.menu {
	display: none;
	position: absolute;
	right: -70px;
	top: 191px;
	&__list {}
	&__item {}
	&__link {
		display: block;
		color: #ffffff;
		font-family: "Proxima Nova", sans-serif;
		font-size: em(13);
		font-weight: 600;
		line-height: 2em;
		text-transform: uppercase;
		&:hover {
			color: #fdb352;
			opacity: 1; } } }
.burger {
	background: transparent;
	border: none;
	float: right;
	margin-top: 15px;

	&__item {
		box-sizing: border-box;
		display: block;
		position: relative;
		border-radius: 10px;
		border: 10px solid #fff;
		border-top: 3px solid;
		border-bottom: 0px solid;
		margin: 4px 0; } }

.logo {
	@include clr;
	display: block;
	color: #fff;
	font-size: em(33);
	@include GP_m;
	line-height: 1.6em;
	&__img {
		float: left;
		margin-right: 18px; }
	&:hover {
		text-decoration: none;
		color: #fff; } }
@include r(1090) {
	.menu {
		right: 0; } }
@include r(650) {
	.menu {
		top: 100%;
		&__link {
			padding: 5px 30px;
			margin: -2px 0;
			background-color: #333;
			border: 2px solid #fdb352;
			opacity: 0.8; } } }
@include r(500) {
	.header {
		padding-top: 14px; } }

@include r(365) {
	.header__lang {
		position: absolute;
		top: 70px;

		margin-right: 0;
		margin-top: 0; } }
